<template>
    <div>
        <el-card>
            <el-descriptions class="margin-top" title="课程信息" :column="2" :size="size" border>
                <template #extra>
                    <el-button :disabled="!isMycourse" type="primary" @click="onupdate">修改信息</el-button>
                </template>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <user />
                            </el-icon>
                            课程名称
                        </div>
                    </template>
                    {{ course.name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            任课老师
                        </div>
                    </template>
                    {{ course.teacher_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            院系
                        </div>
                    </template>
                    {{ course.department }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            年级
                        </div>
                    </template>
                    {{ course.grade }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            创建时间
                        </div>
                    </template>
                    {{ course.create_time }}
                </el-descriptions-item>

                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            默认角色
                        </div>
                    </template>
                    {{ course.default_role_name }}
                </el-descriptions-item>
            </el-descriptions>
            <el-descriptions direction="vertical" :column="1" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            简介
                        </div>
                    </template>
                    <pre>{{ course.introduction }}</pre>
                </el-descriptions-item>
            </el-descriptions>
        </el-card>
        <el-card class="header" v-if="!isRoleManage">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm" style="text-align: center;">
                <el-form-item style="float: left;">
                    <el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-button style="float: left;" type="primary" @click="getDatas">查询</el-button>
                学生管理
                <el-button class="searchBtn" type="primary" @click="importStudent">导入学生</el-button>
                <el-button class="searchBtn" type="primary" @click="managerRole">管理角色</el-button>
            </el-form>

        </el-card>
        <el-card v-if="!isRoleManage">
            <el-table :data="tableData" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="name" label="学生名称" min-width="200">
                </el-table-column>
                <el-table-column label="角色" min-width="200">
                    <template #default="scope">
                        <el-select v-model="scope.row.role_name" @change="changeRole($event, scope.row._id)">
                            <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value" />
                        </el-select>
                    </template>

                </el-table-column>
                <el-table-column prop="class_name" label="班级名称" min-width="200">
                </el-table-column>
                <el-table-column prop="class_department" label="院系" min-width="200">
                </el-table-column>
                <el-table-column align="center" fixed="right" label="操作" width="200">
                    <template #default="scope">
                        <el-button type="danger" @click="exit(scope.row._id)">踢出</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div v-if="tableData.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>

        <!-- 角色管理 -->


        <el-card class="header" v-if="isRoleManage">
            <el-form :inline="true" class="headerForm" :model="searchHeaderForm" style="text-align: center;">
                <el-form-item style="float: left;">
                    <el-input clearable v-model="searchHeaderForm.find" placeholder="请输入名称"></el-input>
                </el-form-item>
                <el-button style="float: left;" type="primary" @click="getDatas">查询</el-button>
                角色管理
                <el-button class="searchBtn" type="primary" @click="submitAddRole">新增角色</el-button>
                <el-button class="searchBtn" type="primary" @click="managerStudent">管理学生</el-button>
            </el-form>

        </el-card>
        <el-card v-if="isRoleManage">
            <el-table :data="tableDataRole" border style="width: 100%"
                :header-cell-style="{ background: '#F3F4F7', color: '#555' }">
                <el-table-column prop="name" label="角色名称" min-width="200">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间" min-width="200">
                </el-table-column>
                <el-table-column prop="update_time" label="修改时间" min-width="200">
                </el-table-column>
                <el-table-column align="center" fixed="right" label="权限" width="200">
                    <template #default="scope">
                        <el-button type="primary" @click="upFunctions(scope.row)">设置权限</el-button>
                    </template>
                </el-table-column>
                <el-table-column align="center" fixed="right" label="操作" width="200">
                    <template #default="scope">
                        <el-button type="primary" @click="submitUpdateRole('update', scope.row)">修改</el-button>
                        <el-button type="danger" @click="deleteRole(scope.row._id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div v-if="tableDataRole.length !== 0" style="display: flex; flex-direction: row-reverse; margin-top: 10px">
                <el-pagination :current-page="currentPage" :page-size="pageSize" :total="total"
                    :page-sizes="[10, 20, 50, 100]" background layout="total, sizes, prev, pager, next, jumper"
                    @size-change="handleSizeChange" @current-change="handleCurrentChange" />
            </div>
        </el-card>




        <el-dialog :modal-append-to-body="false" v-model="operaDialog" :title="修改课程信息" @close="resetFormCrouse">
            <el-form status-icon :model="operaForm" ref="operaForm" class="operaDialog" label-width="100px" :rules="rules">
                <el-form-item label="课程名称">
                    <el-input v-model="operaForm.name" />
                </el-form-item>
                <el-form-item label="院系">
                    <el-input v-model="operaForm.department" />
                </el-form-item>
                <el-form-item label="年级">
                    <el-input v-model="operaForm.grade" />
                </el-form-item>
                <el-form-item label="默认角色">
                    <el-select v-model="operaForm.default_role_id" placeholder="请选择角色" size="large">
                        <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value" />
                    </el-select>
                </el-form-item>
                <el-form-item label="简介">
                    <el-input type="textarea" v-model="operaForm.introduction" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm()">提交</el-button>
                    <el-button @click="resetFormCrouse">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <el-dialog :modal-append-to-body="false" v-model="roleDialog" :title="dialogTitle" @close="resetFormRole">
            <el-form status-icon :model="operaRoleForm" ref="operaRoleForm" class="roleDialog" label-width="100px"
                :rules="rules">
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="operaRoleForm.name" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitRoleForm()">提交</el-button>
                    <el-button @click="resetFormRole">取消</el-button>
                </el-form-item>
            </el-form>

        </el-dialog>
        <el-drawer v-model="drawer" :with-header="false">
            <el-button class="fl-right" type="primary" @click="upFunctionsEnter">确 定</el-button>
            <el-tree ref="functionTree" :data="treedata" :props="defaultProps" :default-checked-keys="functonsTreeIds"
                accordion show-checkbox default-expand-all highlight-current node-key="_id">
            </el-tree>
        </el-drawer>
        <!-- 上传文件导入学生 -->
        <el-dialog :modal-append-to-body="false" v-model="studentDialog" :title="dialogTitle" @close="resetFormRole">
            <el-upload drag v-model:file-list="fileList" name="file" action="http://127.0.0.1:11002/work/course/upload/csv"
                :headers="headers" :on-success="uploadfileSuccess">
                <el-icon class="el-icon--upload"><upload-filled /></el-icon>
                <div class="el-upload__text">
                    上传学生信息文件(.csv)
                </div>
                <template #tip>
                    <div>
                        <el-link type="primary"
                            href="http://101.200.87.232:19000/example/importStudent.xlsx">模板文件下载</el-link>
                    </div>
                </template>
            </el-upload>

        </el-dialog>
    </div>
</template>
<script>
import {
    course_details,
    course_show_student,
    course_update,
    course_exit,
    course_role_create,
    course_role_delete,
    course_role_update,
    course_role_find,
    function_find_by_course,
    course_update_student_role,
} from "@/api/teacher/api.js";
import { toHandlers } from "vue";
// eslint-disable-next-line no-unused-vars
export default {
    data() {
        return {

            course: {},

            operaForm: {},
            headers: {
                token: sessionStorage.getItem('token'),
                user_id: sessionStorage.getItem('user_id'),
                course_id: "",
            },


            operaRoleForm: {},
            tableDataRole: [],
            tableData: [],
            isMycourse: false,//是不是自己的课程
            operaDialog: false,

            isRoleManage: false,
            roleDialog: false,
            dialogTitle: "",

            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            //查询
            searchHeaderForm: {
                find: '',
            },
            roleOpera: "",
            drawer: false,
            updateRole: {},
            treedata: [],
            functonsTreeIds: [],
            defaultProps: {
                children: "functions",
                label: "name",
            },
            options1: [],

            studentDialog: false,

            fileList: [],
        }
    },
    created() {
        this.getUserMessage()
        this.getRoleMessage()
        this.getAllFunctions()
        this.getDatas()
    },
    methods: {
        async getUserMessage() {
            var course_id = this.$route.query.course_id
            course_details({ _id: course_id }).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.course = res.data.data
                    if (this.course.teacher_id == sessionStorage.getItem("user_id")) {
                        this.isMycourse = true
                    }
                    this.headers.course_id = this.course._id
                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        async getDatas() {
            const params = {
                course_id: this.$route.query.course_id,
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
            }
            const res = await course_show_student(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    this.total = res.data.data.count
                    if (res.data.data.data == null) {
                        this.tableData = []
                    } else {
                        this.tableData = res.data.data.data
                    }
                } else {
                    this.total = res.data.data.count
                    this.tableData = []
                }
            }
        },
        onupdate() {
            this.operaDialog = true
            this.operaForm = { ...this.course }//直接赋值是浅拷贝，两者指针相同
            this.operaForm.teacher_id = sessionStorage.getItem("user_id") //防止修改其他人的课程
        },
        submitForm() {
            course_update(this.operaForm).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.getUserMessage()
                    this.$message({
                        showClose: true,
                        message: "修改成功",
                        type: "success",
                    })
                    this.operaDialog = false
                    // this.$router.push('/manager').catch(() => { })
                }
            })

        },
        exit(student_id) {
            var data = {
                student_id: student_id,
                course_id: this.course._id,
            }
            course_exit(data).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.getUserMessage()
                    this.$message({
                        showClose: true,
                        message: "踢出成功",
                        type: "success",
                    })
                    this.getDatas()
                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        async getRoleMessage() {
            this.options1 = []
            const params = {
                course_id: this.$route.query.course_id,
                page: this.currentPage,
                page_size: this.pageSize,
                search: this.searchHeaderForm.find,
            }
            const res = await course_role_find(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    this.total = res.data.data.count
                    if (res.data.data.data == null) {
                        this.tableDataRole = []
                    } else {
                        this.tableDataRole = res.data.data.data
                    }
                    for (let i = 0; i < this.tableDataRole.length; i++) {
                        var option = {
                            value: this.tableDataRole[i]._id,
                            label: this.tableDataRole[i].name,
                        }
                        this.options1.push(option)
                    }
                } else {
                    this.total = res.data.data.count
                    this.tableDataRole = []

                }
            }
        },
        resetFormCrouse() {
            this.operaDialog = false
        },
        resetFormRole() {
            this.roleDialog = false
        },
        managerRole() {
            this.getRoleMessage()
            this.isRoleManage = true
        },
        managerStudent() {
            this.getDatas()
            this.isRoleManage = false

        },
        submitAddRole() {
            this.roleDialog = true
            this.dialogTitle = "新增角色"
            this.roleOpera = "add"
        },
        submitUpdateRole(msg, data) {
            this.roleDialog = true
            this.dialogTitle = "修改角色"
            this.operaRoleForm = { ...data }//直接赋值是浅拷贝，两者指针相同
            this.roleOpera = "update"

        },
        addRole() {
            var data = {
                course_id: this.course._id,
                name: this.operaRoleForm.name
            }
            course_role_create(data).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        showClose: true,
                        message: "创建成功",
                        type: "success",
                    })
                    this.getRoleMessage()

                    // this.$router.push('/manager').catch(() => { })
                }
                this.roleDialog = false
                this.operaRoleForm = {}
            })
        },
        updateRole2() {
            course_role_update(this.operaRoleForm).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        showClose: true,
                        message: "修改成功",
                        type: "success",
                    })
                    this.getRoleMessage()
                }
                this.roleDialog = false
                this.operaRoleForm = {}
            })
        },
        deleteRole(data) {
            course_role_delete({ _id: data }).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        showClose: true,
                        message: "删除成功",
                        type: "success",
                    })
                    this.getRoleMessage()
                }
                this.roleDialog = false
            })
        },
        upFunctions(data) {
            this.drawer = true
            this.functonsTreeIds = data.functions
            this.$nextTick(() => {
                this.$refs.functionTree.setCheckedKeys(this.functonsTreeIds);
            });
            this.updateRole = data
        },
        upFunctionsEnter() {
            const updateAPI = []
            const checkAPIS = this.$refs.functionTree.getCheckedNodes()
            checkAPIS.forEach(item => {
                if (item.functions) return
                updateAPI.push(item._id)
            })
            this.updateRole.functions = updateAPI
            course_role_update(this.updateRole).then(res => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: '权限设置成功',
                        type: "success",
                    })
                } else {
                    this.$message({
                        showClose: true,
                        message: '权限设置失败',
                        type: "error",
                    })
                }
                this.getDatas()
            })
        },
        getAllFunctions() {
            function_find_by_course().then((res) => {
                if (res.data.code === 200) {
                    this.treedata = this.buildApiTree(res.data.data)
                }
            })
        },
        buildApiTree(functions) {
            functions && functions.forEach(item => {
                item.name = item.group + '组'
            })
            return functions
        },
        submitRoleForm() {
            this.$refs.operaRoleForm.validate((valid) => {
                if (valid) {
                    if (this.roleOpera === "add") {
                        this.addRole()
                    } else {
                        this.updateRole2()
                    }
                } else {
                    this.$message({
                        showClose: true,
                        message: "请根据要求将信息填写完整",
                        type: "warning",
                    })
                }

            })
        },
        changeRole(data, d2) {
            var payload = {
                role_id: data,
                _id: d2,
            }

            course_update_student_role(payload).then((res) => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: "修改成功",
                        type: "success",
                    })
                } else {
                    this.$message({
                        showClose: true,
                        message: "修改失败",
                        type: "warning",
                    })
                }
            })
        },
        uploadfileSuccess() {
            this.getDatas();
            this.studentDialog = false;


        },
        importStudent() {
            this.studentDialog = true
        }
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-top: 20px;
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;


        .el-form-item {
            margin-bottom: 0px;
        }
    }
}
</style>
  